import { Home } from '@mui/icons-material';
import './App.css';
import MainApp2 from './MainApp2';
import { HashRouter, Link, Route } from 'react-router-dom';

// Update the --vh custom property whenever the window is resized
function setVhUnit() {
  // Get the viewport height and multiply it by 1% to get a value for 1vh
  let vh = window.innerHeight * 0.01;
  // Set the value in the --vh custom property to the root element
  document.documentElement.style.setProperty('--vh', `${vh}px`);
}

// Initial setting
setVhUnit();

// Listen for window resize and orientation change
window.addEventListener('resize', setVhUnit);
window.addEventListener('orientationchange', setVhUnit);

function App() {
  return (
    <HashRouter>
      <MainApp2 />
    </HashRouter>
  );
}

export default App;
